<template>
  <div>
    {{userId}}
  </div>
</template>

<script>
  import router from '../router/index'
export default {
  name: 'Login',
  props: ['userId'],
  data () {
    return {

    }
  },
  methods: {
    hasCookie () {
      if (this.$cookie.get('userToken') == null) {
        //go to login page
        this.$router.push({name: 'Login'})
      } else {
        this.userId = this.$cookie.get('userId')
      }
    }
  },
  beforeMount() {
    this.hasCookie()
  }
}
</script>

<style scoped>

</style>



